body {
  background-color: #9074ff;
}

.App {
  background-color: #0e0c11;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: rgb(27, 112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 10px;
  color:#fff;
  outline: none;
}

#to-do-form input::placeholder{
  color: rgba(255, 255, 255, 0.5);
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius:5px;
  border: 0;
  background-color: rgb(24, 202, 24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
  outline: none;
}